import {
  useQuery,
  useQueries,
  type UseQueryOptions,
} from '@tanstack/react-query';
import {
  fetchApps,
  fetchDeployments,
  fetchMetrics,
  fetchReleases,
} from '../../api/client.ts';
import { Application, Deployment, Metrics } from '../Applications/types.ts';

export function useAllAppsData() {
  const {
    data: appsData, // shape: { apps: [ { name, ... }, { ... } ] }
    isLoading: isLoadingApps,
    isError: isErrorApps,
    error: appsError,
  } = useQuery({
    queryKey: ['apps'],
    queryFn: fetchApps,
  });

  const apps = appsData?.apps ?? [];

  const deploymentsQueries = useQueries<
    UseQueryOptions<{ deployments: Deployment[] }>[]
  >({
    queries: apps.map((app: Application) => ({
      queryKey: ['deployments', app.name],
      queryFn: () => fetchDeployments(app.name),
      enabled: !!appsData, // Only run if appsData is defined
    })),
  });

  const releasesQueries = useQueries({
    queries: deploymentsQueries.flatMap((deploymentResult, i) => {
      if (!deploymentResult.data || deploymentResult.isError) return [];

      const { deployments } = deploymentResult.data;
      const appName = apps[i].name;

      return deployments.map((dep: Deployment) => ({
        queryKey: ['releases', appName, dep.name],
        queryFn: () => fetchReleases(appName, dep.name),
        enabled: !!deploymentResult.data,
      }));
    }),
  });

  const metricsQueries = useQueries<
    UseQueryOptions<{ metrics: { [key: string]: Metrics } }>[]
  >({
    queries: deploymentsQueries.flatMap((deploymentResult, i) => {
      if (!deploymentResult.data || deploymentResult.isError) return [];

      const { deployments } = deploymentResult.data;
      const appName = apps[i].name;

      return deployments.map((dep: Deployment) => ({
        queryKey: ['metrics', appName, dep.name],
        queryFn: () => fetchMetrics(appName, dep.name),
        enabled: !!deploymentResult.data,
      }));
    }),
  });

  const totalApps = apps.length;

  const totalReleases = releasesQueries.reduce((acc, releaseQuery) => {
    if (!releaseQuery.data) return acc;

    return acc + (releaseQuery.data.history?.length ?? 0);
  }, 0);

  const totalDownloads = metricsQueries.reduce((acc, metricsQuery) => {
    if (!metricsQuery?.data?.metrics) return acc;

    return (
      acc +
      Object.values(metricsQuery.data.metrics).reduce((acc, c) => {
        if (c?.downloaded) {
          return acc + c.downloaded;
        }
        return acc;
      }, 0)
    );
  }, 0);

  const totalSize = releasesQueries.reduce((acc, releaseQuery) => {
    if (!releaseQuery.data) return acc;
    const releases = releaseQuery.data.history ?? [];
    const sumOfReleaseSizes = releases.reduce(
      (sum: number, r: { size?: number }) => sum + (r.size ?? 0),
      0
    );
    return acc + sumOfReleaseSizes;
  }, 0);

  /**
   * 6) Aggregated loading and error states
   */
  const isLoading =
    isLoadingApps ||
    deploymentsQueries.some((q) => q.isLoading) ||
    releasesQueries.some((q) => q.isLoading);

  const isError =
    isErrorApps ||
    deploymentsQueries.some((q) => q.isError) ||
    releasesQueries.some((q) => q.isError);

  return {
    // raw data
    apps,

    // combined info
    totalApps,
    totalReleases,
    totalSize,
    totalDownloads,

    // loading & error
    isLoading,
    isError,
    error: appsError, // or combine error messages from other queries if needed
  };
}
